.model {
    position: absolute;
    top: 0;
    bottom: 0;
    right: -100%;
    z-index: 999;
    max-width: 1000px;
    background: #fff;
    margin: 0 auto;
    padding: 15px;
    transition: all 1s ease;
}
.model.show{right:0}
.view-model{
    max-width: calc(100% - 200px);
    min-height: calc(100vh - 0px);
}
.modelBg{background: rgba(0,0,0,0.2); z-index: 9; position: fixed; height:100%; width:100%; padding: 0; top:0;left:0}

